  left: -100px;
  transition: left 0.5s cubic-bezier(0.33, 0, 0.2, 1) 0s;
}
.moon-sun input:checked + label .stars {
  left: 12px;
  transition: left 0.8s cubic-bezier(0.33, 0, 0.2, 1) 0s;
}


