.flip-box-front{
  transform:rotateY(0deg);
  transform-style: preserve-3d;
}

.flip-box:hover .flip-box-front{
  transform:rotateY(-180deg);
  transform-style: preserve-3d;
}

.flip-box-back{
  position:absolute;
  top:0;
  left:0;
  width:100%;
  transform: rotateY(180deg);
  transform-style: preserve-3d;
}

.flip-box:hover .flip-box-back{
  transform:rotateY(0deg);
  transform-style: preserve-3d;
}

.flip-box .inner{
  position:absolute;
  left:0;
  width:100%;
  padding:60px;
  outline:1px solid transparent;
  perspective: inherit;
  z-index: 2;
  transform: translateY(-50%)translateZ(60px) scale(.94);
  top:50%;
}

 

.flip-box p{
  font-size:20px;
  line-height: 1.5rem;

}

.flip-box-img{
  margin-top:25px;
}

.flip-box-button{
  background-color: blue;
  border:2px solid #fff;
  border-radius: 2px;
  color:#fff;
  cursor:pointer;
  font-size:20px;
  font-weight: bold;
  margin-top:25px;
  padding:15px 20px;
  text-transform: uppercase;


}

