/***********************************************
Form Reset
***********************************************/
form {
 
  
  display: inline-block;
  width: 100%;
}
fieldset {
  margin: 0 0 20px 0;
  padding: 0;
  text-align: left;
  border: none;
  display: block;
  position: relative;
  top: 0;
  left: 0;
}
label {
  margin: 0;
  padding: 0 0 6px;
  width: 100%;
  display: block;
  font-size: 14px;
  line-height: 18px;
  color: #555;
  font-weight: bold;
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
}
input,
textarea,
select {
  margin: 0;
  padding: 0 10px;
  display: block;
  border-radius: 0;
  width: 100%;
  height: 40px;
  font-size: 14px;
  line-height: 40px;
  font-weight: bold;
  color: #555;
  border-width: 1px;
  border-style: solid;
  overflow: hidden;
  position: relative;
  top: 0;
  left: 0;
  outline: 0 none;
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-appearance: none;
  transition: all 0.2s cubic-bezier(0.33, 0, 0.2, 1) 0s;
  -moz-transition: all 0.2s cubic-bezier(0.33, 0, 0.2, 1) 0s;
  -webkit-transition: all 0.2s cubic-bezier(0.33, 0, 0.2, 1) 0s;
  -o-transition: all 0.2s cubic-bezier(0.33, 0, 0.2, 1) 0s;
}

/***********************************************
Sun/Moon Checkbox styling
***********************************************/
.moon-sun {
  margin: 0 auto;
  padding: 8px 0 0;
  width: 122px;
  display: block;
  overflow: hidden;
  border: none;
}
.moon-sun input {
  display: none;
}
.moon-sun input + label {
  width: 100%;
  height: 64px;
  background-color: #46a1f5;
  border-radius: 32px;
  border: 6px solid #23517b;
  overflow: hidden;
  position: relative;
  top: 0;
  left: 0;
  z-index: 9;
  cursor: pointer;
  transition: background 0.5s cubic-bezier(0.33, 0, 0.2, 1) 0s,
    border 0.5s cubic-bezier(0.33, 0, 0.2, 1) 0s;
}
.moon-sun input + label span {
  width: 40px;
  height: 40px;
  background-color: #faa31b;
  border-radius: 20px;
  display: block;
  position: absolute;
  top: 6px;
  left: 6px;
  transition: left 0.5s cubic-bezier(0.33, 0, 0.2, 1) 0s,
    background 0.5s cubic-bezier(0.33, 0, 0.2, 1) 0s;
}

.moon-sun input:checked + label {
  background-color: #4e30ad;
  border-color: #271857;
}
.moon-sun input:checked + label span {
  width: 40px;
  height: 40px;
  background-color: #fff;
  border-radius: 20px;
  display: block;
  left: calc(50% + 9px);
}

.moon-sun input + label span:before {
  content: "";
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: #46a1f5;
  position: absolute;
  top: -10px;
  left: -40px;
  transition: background 0.5s cubic-bezier(0.33, 0, 0.2, 1) 0s,
    left 0.5s cubic-bezier(0.33, 0, 0.2, 1) 0s;
}
.moon-sun input:checked + label span:before {
  background-color: #4e30ad;
  left: -15px;
}

.moon-sun .left,
.moon-sun .right {
  color: #ddd;
  height: 64px;
  font-size: 20px;
  line-height: 64px;
  position: absolute;
  top: 8px;
  right: 50%;
  z-index: 8;
  transition: color 0.5s cubic-bezier(0.33, 0, 0.2, 1) 0s;
}

.moon-sun .left {
  padding: 0 70px 0 0;
  color: #faa31b;
}
.moon-sun .right {
  padding: 0 0 0 70px;
  right: auto;
  left: 50%;
}

.moon-sun input:checked + label + .left {
  color: #ddd;
}
.moon-sun input:checked + label + .left + .right {
  color: #4e30ad;
}

